.bottomWallet {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 24px;
  cursor: pointer;
  &:hover {
    background: #202123;
  }
  .ventricleLine {
    width: 1px;
    height: calc(~'100% - 8px');
    margin: 4px;
  }
  .mesContent {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 4px;
    &.warning {
      color: #cf0002;
      .mesValue {
        max-width: initial;
      }
    }
    .mesValue {
      max-width: 80px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}
