.walletWrapper {
  position: fixed;
  right: 50px;
  bottom: 42px;
  width: 256px;
  height: 246px;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
  .walletContent {
    .accountDetail {
      margin: 0 16px;
      .networkStatus {
        height: 19px;
        margin: 8px 0;
        line-height: 19px;
        .dot {
          display: inline-block;
          width: 6px;
          height: 6px;
          border-radius: 50%;
        }
        span {
          height: 19px;
          margin-left: 8px;
          line-height: 19px;
        }
      }
      .accountName {
        height: 19px;
        line-height: 19px;
        text-align: center;
      }
      .accountAddress {
        height: 19px;
        line-height: 19px;
        text-align: center;
        .address {
          padding-right: 2px;
          padding-left: 18px;
        }
      }
      .accountBalance {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 32px 12px;
        font-weight: 600;
        font-size: 21px;
        .balance {
          max-width: 60%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        span {
          padding-left: 4px;
        }
      }
    }
    .accountList {
      .listWrapper {
        height: 140px;
        margin: 8px 0;
        ::-webkit-scrollbar {
          width: 0;
          height: 0;
        }
        .accountItem {
          display: flex;
          align-items: center;
          padding: 6px 16px;
          cursor: pointer;
          .dot {
            width: 6px;
            height: 6px;
            margin-right: 8px;
            background-color: transparent;
            border-radius: 50%;
          }
          .accountInfo {
            display: flex;
            flex: 1;
            align-items: center;
            .content {
              flex: 1;
              .top {
                display: flex;
                justify-content: space-between;
                height: 17px;
                .name {
                  width: 60%;
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
                .balance {
                  width: 40%;
                  text-align: right;
                  span {
                    padding-left: 4px;
                  }
                }
              }
              .bottom {
                display: flex;
                justify-content: space-between;
                height: 17px;
              }
            }
          }
        }
      }
      .createBtn {
        padding: 8px 22px;
        font-size: 14px;
      }
    }
    .createAccount {
      margin: 0 16px;
      .title {
        height: 19px;
        margin: 8px 0;
        line-height: 19px;
      }
      .inputName {
        margin: 8px 16px 16px;
      }
      .btnWrapper {
        display: flex;
        justify-content: space-between;
        margin: 0 16px;
      }
    }
  }
}

.warning {
  display: block;
  margin: 0 0 10px;

  .dot {
    display: inline-block;
    width: 6px;
    height: 6px;
    margin-right: 8px;
    border-radius: 50%;
  }
}
